var string = "<% var imgURL = model.get('headshot') %>\n<img id='avatar' height='150' src=\"<%= imgURL || '/vendor/img/avatar.svg' %>\">\n\n<table>\n  <tr data-attribute='name'>\n    <th>Name:</th>\n    <td><span class='profile-field' id='first_name'><span class='p'><%= model.get('first_name') %></span><span class='form'><form><input name='first_name' type='text' value='<%= model.get('first_name') %>' /></form></span></span> <span class='profile-field' id='last_name'><span class='p'><%=model.get('last_name') %></span><span class='form'><form><input name='last_name' type='text' value='<%= model.get('last_name') %>' /></form></span></span></td>\n  </tr>\n  <tr data-attribute='username'>\n    <th>Username:</th>\n    <td>\n      <span class='profile-field' id='username'>\n        <span class='p'><%= model.get('username') %></span>\n        <span class='form'><form><input name='username' type='text' value='<%= model.get('username') %>'></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='email'>\n    <th>E-mail:</th>\n    <td>\n      <span class='profile-field' id='email'>\n        <span class='p'><%= model.get('email') %></span>\n        <span class='form'><form><input name='email' type='email' value='<%= model.get('email') %>' /></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='birthdate'>\n    <th>Birthdate:</th>\n    <td>\n      <span class='profile-field' id='birthdate'>\n        <span class='p'><%= model.get('birthdate') || 'N/A' %></span>\n        <span class='form'><form><input name='birthdate' type='date' value='<%= model.get('birthdate') %>' /></form></span>\n      </span>\n    </td>\n  </tr>\n  <tr data-attribute='fach'>\n    <th>Fach:</th>\n    <td>\n      <span class='profile-field' id='fach'>\n        <span class='p'><%= model.get('fach') || 'N/A' %></span>\n        <span class='form'><form><input name='fach' value='<%= model.get('fach') %>' /></form></span>\n      </span>\n    </td>  </tr>\n  <tr data-attribute='city'>\n    <th>City:</th>\n    <td>\n      <span class='profile-field' id='city'>\n        <span class='p'><%= model.get('city') || 'N/A' %></span>\n        <span class='form'><form><input name='city' value='<%= model.get('city') %>' /></form></span>\n      </span>\n    </td>  </tr>\n  <tr>\n    <th>Member Since:</th>\n    <td><%= model.prettyCreateDate() %></td>\n  </tr>\n</table>";

module.exports = string;
