var string = "<span class='pull-right edit-task'>\n  <i class='fa fa-archive fa-fw' title='Backlog'></i>  <i class='fa fa-pencil fa-fw' title='Edit'></i>  <i class='fa fa-times fa-fw' title='Delete'></i>\n</span>\n<table>\n  <tr>\n    <td style='vertical-align: top'><i class='fa fa-square-o' title=\"Mark complete\"></i></td>\n    <td class='task-listing'>\n      <a href='' class='task-title' title=\"<%- model.get('title') %>\"><%- model.displayTitle(width) %></a>\n      <table class='task-details'>\n        <% if (model.get('deadline')) { %>\n          <tr class='task-deadline-row'>\n            <th>Deadline:</th>\n            <td><%- model.prettyDeadline() %></td>\n          </tr>\n        <% } %>\n        <tr class='task-priority-row'>\n          <th>Priority:</th>\n          <td><%- model.get('priority') %></td>\n        </tr>\n        <tr class='task-status-row'>\n          <th>Status:</th>\n          <td><%- model.get('status') %></td>\n        </tr>\n        <% if (model.get('description')) { %>\n          <tr class='task-description-row'>\n            <th style='vertical-align: text-top'>Notes:</th>\n            <td><%- model.get('description') %></td>\n          </tr>\n        <% } %>\n      </table>\n    </td>\n  </tr>\n</table>";

module.exports = string;
